import { useContext } from 'react';
import { Routes, Route } from 'react-router-dom'
import { context } from '../../context/context';
import { AllChatsList } from '../../pages/AllChatsList/AllChatsList';
import { Catalog } from '../../pages/Catalog/Catalog';
import { Chat } from '../../pages/Chat/Chat';
import { CreateProfile } from '../../pages/CreateProfile/CreateProfile';
import { Main } from '../../pages/Main/Main';
import { ProfileDetails } from '../../pages/ProfileDetails/ProfileDetails';
import { Layout } from '../Layout/Layout';

function App() {
  const ctx = useContext(context)

  return (
    <>
      <Routes>
        <Route path="/" element={<Main user={ctx.user} />} />
        <Route path="/create" element={
          <Layout backLink={ctx.backLink}>
            <CreateProfile />
          </Layout>} />
        <Route path="/catalog" element={
          <Layout user={ctx.user} backLink={ctx.backLink}>
            <Catalog />
          </Layout>} />
        <Route path="/catalog/:profileId" element={
          <Layout user={ctx.user} backLink={ctx.backLink?.pathname}>
            <ProfileDetails />
          </Layout>} />
        <Route path="/chat" element={
          <Layout user={ctx.user} backLink={ctx.backLink} visible={ctx.chatFooter}>
            <Chat />
          </Layout>} />
        <Route path="/chatslist" element={
          <Layout user={ctx.user} backLink={ctx.backLink} visible={ctx.chatFooter}>
            <AllChatsList />
          </Layout>} />
      </Routes>
    </>
  );
}

export default App;
